const FarcasterConnectView = ({ url }) => {
    const { theme: { theme }, } = useThemeContext();
    const iconThemeVariant = useIconThemeVariant();
    const icon = jsx(SocialIcon, { name: 'farcaster', variant: iconThemeVariant });
    return (jsx(QrCodeWrapper, { uri: url, accentColor: iconThemeVariant === 'dark' ? theme.colors.textPrimary : '#7b65c0', icon: icon, showCopyToClipboardButton: true }));
};

export { FarcasterConnectView };
